@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lato:wght@300;400;700&family=Montserrat&display=swap');

* {
    margin: 0;
    padding: 0;
    list-style: none;
    box-sizing: border-box;
    color: #2B2D42;
    font-family: 'Lato', sans-serif;
    font-size: 18px;

}

h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: 400;
    padding-bottom: 10px;
    margin: 20px 0;
    border-bottom: 1px solid #DBA81A;
    box-shadow: 0px 15px 10px -15px black 0.25;
    text-transform: uppercase;
}

.lato {
    font-family: 'Lato', sans-serif;
    font-size: 24px;
}

.bebas {
    font-family: 'Bebas Neue', cursive;
    font-size: 32px;
}

.italic {
    font-style: italic;
}

.regular {
    font-weight: 400;
}

.light {
    font-weight: 300;
    color: #8D99AE;
}

.bold {
    font-weight: 700;
}

.ocher {
    color: #DBA81A;
}

.list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 50px;
}

nav {
    margin: 25px 0;
    transition: 0.2s;
}

nav.sticky {
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 100;
}

.nav-items {
    display: flex;
    justify-content: space-evenly
}

li.nav-item {
    font-family: 'Bebas Neue', cursive;
    font-size: 32px;
}


.publicationList {
    margin-bottom: 50px;
}

ul .list__style {
    list-style: none;
    /* Remove default bullets */
}
ul li.list__style::before {
    content: "\2022";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: #DBA81A; /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: inline-block; /* Needed to add space between the bullet and the text */
    width: 1em; /* Also needed for space (tweak if needed) */
    margin-left: -1em; /* Also needed for space (tweak if needed) */
  }

@media (min-width: 997px) {
    .App {
        margin: 0 100px;
    }
}

@media (min-width: 501px) and (max-width: 996px) {
    .App {
        margin: 0 50px;
    }
}

@media (max-width: 500px) {
    .App {
        margin: 0 50px;
    }
}